.sidebar {
    display: none;
}

.sidebar.open {
    display: block;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
}

.nav {
    font-family: Montserrat, sans-serif;
    position: fixed;
    top: 0;
    left: 0;
    width: 317px;
    height: 100%;
    background-color: #fff;
    padding: 60px 70px;
    display: flex;
    flex-direction: column;
    z-index: 20;
    gap: 45px;

    font-weight: 600;
    font-size: 22px;
}

.nav a {
    text-decoration: none;
    color: black;
}

.nav a:hover {
    color: #58acff;
}

.nav a.active {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #0059b2;
}

.close {
    position: fixed;
    top: 16px;
    left: 270px;
    width: 24px;
    z-index: 30;
    border: none;
    background: none;
    cursor: pointer;
}

.close img {
}
